* {
  box-sizing: border-box;
}

html, body {
  --border-radius: 100px;
  --blue: #1c47ff;
  --pink: #d6a3fb;
  --green: #d0ff94;
  --grey: #e5e5e5;
  --gutter: 3.125vw 6.25vw;
  color: #fff;
  background-color: #000;
  margin: 0;
  padding: 0;
  font-family: Syne, sans-serif;
  font-size: 25px;
}

h1 {
  margin: 0;
  font-size: 70px;
  line-height: 1em;
}

h2 {
  margin: 0;
  font-size: 50px;
}

a {
  color: #fff;
  text-decoration: none;
}

section {
  z-index: 2;
  width: 100%;
  height: calc(100vh - 100px);
  display: flex;
  position: relative;
  top: 100px;
}

section.sticky {
  margin-top: 100px;
  position: sticky;
}

section.full-screen {
  z-index: 3;
  height: 100vh;
  margin-top: 0;
  top: 0;
}

nav {
  z-index: 1;
  width: 100%;
  height: 100px;
  font-size: 20px;
  position: fixed;
  top: 0;
  left: 0;
}

nav ul {
  align-items: center;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
}

nav ul li {
  margin-left: 45px;
  list-style: none;
}

nav ul li a {
  color: #fff;
  font-family: Space Mono, monospace;
}

nav ul li {
  transition: font-size .3s;
}

nav ul li li:hover {
  font-size: 30px;
}

section.about .empty {
  background-color: var(--grey);
  border-radius: var(--border-radius);
  flex-basis: 37.5vw;
  transition: flex-basis 1s cubic-bezier(0, .86, .52, 1);
}

section.about article {
  flex-direction: column;
  flex: 1;
  display: flex;
}

section.about article .title {
  background-color: var(--blue);
  border-radius: var(--border-radius);
  padding: var(--gutter);
  flex: 1;
  align-items: flex-end;
  transition: flex-basis 1s cubic-bezier(0, .86, .52, 1);
  display: flex;
}

section.about article .description {
  border-radius: var(--border-radius);
  padding: var(--gutter);
  color: #1f1a1c;
  background-color: var(--pink);
  flex: 1;
  align-items: center;
  display: flex;
}

section.services article {
  width: 100%;
  display: flex;
}

section.services figure {
  background-color: var(--green);
  border-radius: var(--border-radius);
  flex-basis: 56.25vw;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
}

section.services figure img {
  width: 40%;
}

section.services .text {
  padding: var(--gutter);
  border-radius: var(--border-radius);
  background-color: #1f1a1c;
  flex-direction: column;
  flex: 1;
  justify-content: center;
  display: flex;
}

section.services .text a {
  font-family: Space Mono, monospace;
}

section.services.services-html figure img {
  animation-name: rotate;
  animation-duration: 3s;
  animation-timing-function: cubic-bezier(.24, .82, .69, .37);
  animation-iteration-count: infinite;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  33.33% {
    transform: rotate(120deg);
  }

  66.66% {
    transform: rotate(240deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

section.services.services-html .text {
  transition: background-color 3s cubic-bezier(.24, .82, .69, .37), color 3s cubic-bezier(.24, .82, .69, .37);
}

section.services.services-html .text:hover {
  background-color: var(--blue);
  color: #1f1a1c;
}

section.services.services-css figure {
  animation-name: changeColor;
  animation-duration: 3s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
}

@keyframes changeColor {
  0% {
    background-color: var(--blue);
  }

  33.33% {
    background-color: var(--green);
  }

  66.66% {
    background-color: var(--pink);
  }

  100% {
    background-color: var(--blue);
  }
}

section.services.services-css .text {
  transition: background-color 3s cubic-bezier(.24, .82, .69, .37), color 3s cubic-bezier(.24, .82, .69, .37);
}

section.services.services-css .text:hover {
  background-color: var(--pink);
  color: #1f1a1c;
}

section.services.services-js figure img {
  animation-name: loop;
  animation-duration: 1s;
  animation-timing-function: cubic-bezier(.24, .82, .69, .37);
  animation-iteration-count: infinite;
  animation-direction: alternate-reverse;
}

@keyframes loop {
  0% {
    transform: translateX(50%);
  }

  100% {
    transform: translateX(-50%);
  }
}

section.services.services-js .text {
  transition: background-color 3s cubic-bezier(.24, .82, .69, .37), color 3s cubic-bezier(.24, .82, .69, .37);
}

section.services.services-js .text:hover {
  background-color: var(--green);
  color: #1f1a1c;
}

section.services.services-html figure {
  background-color: var(--green);
}

section.services.services-html .text h2, section.services.services-html .text a {
  color: var(--pink);
}

section.services.services-css figure {
  background-color: var(--blue);
}

section.services.services-css .text h2, section.services.services-css .text a {
  color: var(--green);
}

section.services.services-js figure {
  background-color: var(--pink);
}

section.services.services-js .text h2, section.services.services-js .text a {
  color: var(--blue);
}

section.contact a {
  color: #1f1a1c;
  font-size: 65px;
  font-weight: bold;
}

section.contact {
  background: linear-gradient(var(--blue), var(--green) 55%, var(--pink) 63%);
  border-radius: var(--border-radius);
  justify-content: center;
  align-items: center;
}

section.contact a {
  transition: color 2s cubic-bezier(.24, .82, .69, .37);
}

section.contact a:hover {
  color: #fff;
}

@media only screen and (width <= 768px) {
  html, body {
    --border-radius: 50px;
    --gutter: 6.25vw;
    font-size: 20px;
  }

  h1, h2 {
    font-size: 40px;
  }

  nav ul li {
    margin-left: 25px;
  }

  section.services article {
    flex-direction: column;
  }

  section.services figure {
    flex-basis: 25vh;
  }

  section.services figure img {
    width: 20%;
  }

  section.contact a {
    font-size: 30px;
    transform: rotate(90deg);
  }
}
